<template>
  <div class="circular">
    <h4 v-if="title" class="font-bold text--header text-center mb-2 mt-1">{{ title }}</h4>
    <div class="wrapper relative mx-auto">
      <div class="progress-percent inline-block absolute">{{ progressRef }}%</div>
      <div class="percent">
        <svg>
          <circle :cx="size + strokeWidth" :cy="size + strokeWidth" :r="size - strokeWidth" />
          <circle :cx="size + strokeWidth" :cy="size + strokeWidth" :r="size - strokeWidth" />
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from 'vue';

export default {
  props: {
    size: {
      type: Number,
      default: 50,
    },
    strokeWidth: {
      type: Number,
      default: 5,
    },
    progress: {
      type: Number,
      default: 70,
    },
    speed: {
      type: String,
      default: '15s',
    },
    title: {
      type: String,
      default: '',
    },
    transparent: {
      type: Boolean,
      default: true,
    },
    stroke: {
      type: Boolean,
      default: true,
    },
  },
  setup(props) {
    const progressRef = ref(0);
    const speedNumber = props.speed.match(/\d/g).join('');
    const strokeLength = Math.ceil(2 * props.size * Math.PI) - props.progress / 2;

    onMounted(() => {
      const intervalId = setInterval(() => {
        progressRef.value += 1;

        if (progressRef.value === props.progress) {
          clearInterval(intervalId);
        }
      }, Math.ceil((speedNumber * 25) / 10));
    });

    return {
      sizePx: computed(() => `${props.size * 2 + props.strokeWidth}px`),
      strokeWidthPx: computed(() => `${props.strokeWidth}px`),
      startProgress: computed(() => strokeLength - (strokeLength * 0) / 100),
      endProgress: computed(() => strokeLength - (strokeLength * props.progress) / 100),
      circleBg: computed(() => (props.transparent ? 'transparent' : 'false')),
      strokeLine: computed(() => (props.stroke ? 'var(--text-color)' : 'none')),
      progressRef,
    };
  },
};
</script>

<style lang="scss" scoped>
$rotateDeg: 270deg;

.wrapper {
  width: v-bind(sizePx);
  height: v-bind(sizePx);
  overflow: hidden;

  .progress-percent {
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    z-index: 1;
  }
}

.percent {
  rotate: $rotateDeg;

  svg {
    width: v-bind(sizePx);
    height: v-bind(sizePx);
    fill: v-bind(circleBg);
  }
  svg circle {
    stroke: v-bind(strokeLine);

    &:nth-child(2) {
      stroke: var(--hightlight-color);
      stroke-width: v-bind(strokeWidthPx);
      stroke-dasharray: v-bind(startProgress);
      // stroke-dashoffset: v-bind(endProgress);
      animation: aniProgress v-bind(speed) forwards;
    }
  }
}

@keyframes aniProgress {
  0% {
    stroke-dashoffset: v-bind(startProgress);
  }
  25% {
    stroke-dashoffset: v-bind(endProgress);
  }
  // delay animation
  100% {
    stroke-dashoffset: v-bind(endProgress);
  }
}
</style>
